
.footer {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 24px;
  flex-shrink: 0;
}

.footer-text {
  text-align: center;
  font-size: 12px;
  line-height: 22px;
  color: #000000;
  opacity: 0.3;
  height: 22px;
}

.footer-text > a {
  color: #000;
  text-decoration: none;
  cursor: pointer;
}

.footer-text > a:hover {
  text-decoration: underline;
}
