.wrapper {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
}

.progress-bar {
  height: 4px;
  background: #FFAC46;
  flex-shrink: 0;
  transition: width .5s ease-in-out;
}

.main {
  flex: 1;
  height: 100%;
  width: 100%;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  max-width: 1440px;
}


.main > .container {
  display: flex;
  flex: 1;
  flex-shrink: 0;
    overflow-y: scroll;
}

.bottom-mask-wrapper {
  width: 100%;
  animation: bottomslideup 1.2s cubic-bezier(0.25, 0.1, 0.25, 1) 0s;
  position: fixed;
  left: 0;
  bottom: 0;
  z-index: 1;
  height: 60px;
  background: #1F1F21;
}

.bottom-mask {
  width: 100%;
  max-width:1440px;
  height: 60px;
  display: flex;
  align-items: center;
  padding: 0 22px 0 200px;
  margin: 0 auto;
  justify-content: space-between;

}
.bottom-mask > div {
  display: flex;
  align-items: center;
}

.bottom-mask > div > p {
  height: 22px;
  font-size: 16px;
  line-height: 22px;
  color: #fff;
  margin-right: 12px;
}

.bottom-mask-ok {
  width: 36px;
  height: 36px;
}


@keyframes bottomslideup {
  from {
    bottom: -60px;
    opacity: 0;
    display: none;
  }

  to {
    bottom:  0;
    opacity: 100;
    display: flex;
  }
}



@media screen and (max-width: 1440px)  and (min-width: 1024px){

  .main {
    max-width: 1024px;
  }

  .bottom-mask {
    max-width: 1024px;
  }
}


/** 320 - 768*/
@media screen and (max-width: 1024px) and (min-width: 320px) {

  .bottom-mask > div > p {
    font-size: .14rem;
  }
  .bottom-mask {
    padding: 0;
  }

  .bottom-mask > div{
    width: 100%;
    display: flex;
    justify-content: center;
  }

}
