.home {
  display: flex;
  flex-direction: column;
  width: 100%;
  padding: 0 76px 0 200px;
}

.home-main {
  width: 100%;
  display: flex;
  flex-direction: column;
  /* justify-content: space-between;
  align-items: center; */
  margin-top: 136px;
  flex-shrink: 0;
}

.home-main-header {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 50px;
  width: 224px;
  padding: 10px;
  background-color: #262627;
  margin-bottom: 24px;
  /* animation: slidetoright 2s cubic-bezier(0.25, 0.1, 0.25, 1) 0.2s; */
}


@keyframes slidetoright {
 from {
  width: 0px;
  opacity: 0;
 }

 to {
  width: 224px;
  opacity: 1;
 }
}


.home-main-body {
  display: flex;
  justify-content: space-between;
  margin-bottom:27px;
  flex-shrink: 0;
}


.info {
  display: flex;
  flex-direction: column;
}

.info-logo {
  display: none;
}

.info-desc {
  width: 600px;
  color: #262627;
  margin-bottom: 54px;
  font-weight: 600;
  font-size: 42px;
  line-height: 66px;
  display: flex;
  flex-direction: column;
}

.button-group {
  display: flex;
  align-items: center;
  height: 42px;
}

.button-group > button:first-child {
  margin-right: 14px;
}

.extra {
  display: flex;
  align-items: center;
  margin-bottom: 18px;
  flex-shrink: 0;
}

.extra-title {
  color: #262627;
  text-decoration: none;
}

.extra-title  {
  color: #262627;
  text-decoration: none;
  cursor: pointer;
}


.extra-title:hover {
  text-decoration: underline
}

.extra-link-icon {
  width: 18px;
  height: 18px;
}


.banner-logo {
  width: 400px;
  height: 400px;
  display: flex;
  align-items: center;
  justify-content: center;
  transform: translate3d(0px, -40px, 0);
}


@keyframes logoslideleft {
  from {
    right: -94px;
    opacity: 0,
  }

  to {
    right: 0;
    opacity: 1;
  }
}

.banner-logo > img {
  width: 400px;
  height: 400px;
}

.logo-shadow-wrapper {
  display: flex;
  flex-direction: row-reverse;
  padding-right: 50px;
  width:100%;
  margin-bottom: 28px;
}

.logo-shadow {
  width: 272px;
  height: 66px;
}

.bottom-line {
  display: block;
  width: 1164px;
  height: 10px;
  background: url('@/assets/bottom-arrow-line.svg') no-repeat;
  background-size: cover;
  margin-bottom: 60px;
}




/** 1024 - 1440 */
@media screen and (max-width: 1440px) {
  .home {

  padding: 0 54px 0 142px;
}

.home-main {
  width: 100%;
  display: flex;
  margin-top: 96.7px;
}


  .home-main-header {
    display: flex;
  align-items: center;
  justify-content: center;
  height: 35px;
  width: 159px;
  padding: 7px;
  background-color: #262627;
  margin-bottom: 17px;
  }

  .home-main-body {
  display: flex;
  justify-content: space-between;
  margin-bottom:19.2px;
}

  .info-desc {
  width: 426px;
  color: #262627;
  margin-bottom: 38.4px;
  font-weight: 600;
  font-size: 30px;
  line-height: 47px;
  display: flex;
  flex-direction: column;
  }

.button-group {
  display: flex;
  align-items: center;
  height: 30px;
}

.button-group > button:first-child {
  margin-right: 10px;
}

.banner-logo {
  width: 284px;
  height: 284px;
  display: flex;
  align-items: center;
  justify-content: center;
  transform: translate3d(0px, -28.4px, 0);
}

.extra {
  display: flex;
  align-items: center;
  margin-bottom: 12.8px;
}

.extra > p {
  font-size: 14px;
}

.extra-icon {
  width: 9.6px;
  height: 9.6px;
  margin-right: 4.6px;
}

.extra-title {
  margin-right: 5.68px;
  font-size: 14px;
  height: 14px;
  line-height: 14px;
}

.extra-link-icon {
  width: 12.8px;
  height: 12.8px;
}

 .banner-logo {
   width:  284px;
   height: 284px;
 }

 .banner-logo > img {
  width: 284px;
  height: 284px;
 }

 .logo-shadow-wrapper {
  padding-right: 35.5px;
  margin-bottom: 20px;
}

.logo-shadow {
  width: 193px;
  height: 47px;
}

.bottom-line {
  display: block;
  width: 827px;
  height: 7px;
  background: url('@/assets/bottom-arrow-line.svg') no-repeat;
  background-size: cover;
  margin-bottom: 42.6px;
}
}


/** 320 - 1024*/
@media screen and (max-width: 1024px) and (min-width: 320px) {
  /* body {
     position: fixed;
  } */

  .home {
    padding: 0;
    background: url(@/assets/bg-sm/bg.png) no-repeat;
    background-size: cover;
    overflow-x: hidden;
  }

  .home-main {
    margin-top: .3rem;
    margin-bottom: .5rem;
  }

  .home-main-header {
    display: none;
  }

  .home-main-body {
    flex-direction: column-reverse;
    align-items: center;
  }

  .bottom-line {
    display: none;
  }

  .banner-logo {
    width: 2.7rem;
    height: 2.7rem;
    display: flex;
    align-items: center;
    justify-content: center;
    animation: unset;
    transform: unset;
    /* right: -94px; */
    flex-shrink: 0;
    margin-bottom: 40px;
  }

  .banner-logo > img {
    width: 2.7rem;
    height: 2.7rem;
  }


  .info {
    padding: 0 .33rem;
    display: flex;
    flex-direction: column;
    flex-shrink: 0;
  }

  .info-logo {
    width: 1.58rem;
    height: .36rem;
    background: #262627;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: .08rem;
  }

  .info-logo  img {
    margin-bottom: 0;
  }

  .info-desc {
    width: 3.15rem;
    margin-bottom: .2rem;
    font-weight: 600;
    font-size: .28rem;
    line-height: .46rem;
  }

  .button-group {
    width: 100%;
    display: flex;
    flex-direction: column;
  }

   .button-group > button {
    width: 100%;
    height: .42rem;
    flex-shrink: 0;
    font-size: .16rem;
    line-height: .16rem;
  }

  .button-group > button:first-child {
    margin-bottom: 0.17rem;
    margin-right: 0;
  }

  .button-group > button > img {
    width: .16rem;
    height: .16rem;
    margin-right: .08rem;
  }


  .extra {
    justify-content: center;
    margin-bottom: .8rem;
    margin-top: .30rem;
  }

  .extra > p {
    margin-top: .3rem;
  }
}
